	//import auth from '../../components/auth.vue'
	export default {
		components:{
			
		},
		data() {
			return {
				userlookhousetime: '',
				userphone: '',
				username: '',
				title: 'Hello',
				swiperList: 5,
				current: 0,
				bannerList: [],
				video: '',
				phone: '',
				memorabilia:{}, 
				actvity:{},
				role: uni.getStorageSync('role') //登录获取,1为普通用户
			};
		},
		onLoad(option) {
			if(option.id){
				let that=this
				uni.getSetting({
				   success(res) {
				      console.log(res.authSetting)
					  if(!res.authSetting["scope.userInfo"]){
						  uni.navigateTo({
						  	url: '/pages/auth/auth'
						  });
					  }else{
						 let token=uni.getStorageSync('token')
						 this.$http.bind(option.id,token).then(res=>{
						 	
						 }) 
					  }
				   }
				})
				
			}

		},
		methods: {
			jumpzixun(data) {
				console.log(data);
				//+encodeURIComponent(JSON.stringify(data))
				 uni.setStorageSync('newsData',JSON.stringify(data));
				uni.navigateTo({
					url: '/pages/zixun/news'
				});
			},
			jumpzixunlist(index) {
				uni.navigateTo({
					url: '/pages/zixun/index'
				});
			},
			jumpjijin() {
				uni.navigateTo({
					url: '/pages/house_photo_list/index'
				});
			},
			jumpshapan() {
				uni.navigateTo({
					url: '/pages/shapan/index'
				});
			},
			jumphuxing() {
				uni.navigateTo({
					url: '/pages/housetype/index'
				});
			},
			jumpvr() {
				uni.navigateTo({
					url: '/pages/webview/index'
				});
			},
			jumpprogress() {
				uni.navigateTo({
					url: '/pages/progress/index'
				});
			},
			jumpxuanchuan() {
				uni.navigateTo({
					url: '/pages/3d_show/index'
				});
			},
			jumpshow() {
				uni.navigateTo({
					url: '/pages/park/index'
				});
			},
			jumpseven() {
				uni.navigateTo({
					url: '/pages/seven_type/index'
				});
			},
			jumpimglist() {
				uni.navigateTo({
					url: '/pages/3d_show/index1'
				});
			},
			changeHandle(e) {
				this.current = e.detail.current;
			},
			callPhone() {
				console.log(this.phone);
				uni.makePhoneCall({
					phoneNumber: this.phone //仅为示例
				});
			},
			scrollTolast(diff) {
				uni.pageScrollTo({
					scrollTop: diff,
					duration: 300
				});
			},
			toMap(){
				uni.openLocation({
					latitude:25.022151,
					longitude: 102.652347,
					name: "昆明融创文旅城",
					address: "昆明市西山区碧鸡路和益宁路交叉口"
				})
				// uni.navigateTo({
				// 	url: '/pages/map/index'
				// });
			},
			gotoChat() {
                let that=this
                uni.getSetting({
                   success(res) {
					   console.log(res);
                	  if(!res.authSetting["scope.userInfo"]){
                		uni.navigateTo({
                			url: '/pages/auth/auth'
                		});
                	  }else{
                	uni.requestSubscribeMessage({
                		tmplIds: ['yLyxJKwUOVitKFREPciNHvfI4-kPMkQPlr1tGQ1U3D4'],
                		success: (res) => {
                			if (res["yLyxJKwUOVitKFREPciNHvfI4-kPMkQPlr1tGQ1U3D4"] === 'accept') {
                				let token=uni.getStorageSync('token')
                				this.$http.addPush(token).then(res => {
                					console.log(res);
                					uni.navigateTo({
                						url: '/pages/message/index'
                					});
                				})
                			}
                		}
                	})
                	  }
                   }
                })
			
			},
			gotoOtherSmProgram() { //打开其他小程序
				uni.navigateToMiniProgram({
					appId: 'wx2f7d0154d4adc63d',
					path: '',
					extraData: {
						
					},
					success(res) {
						// 打开成功
						console.log(res);
					},
					fail(err){
						console.log(err);
					}
				})
			},
			yuyue() {

				if (this.userphone.length <= 0) {
					return uni.showToast({
						title: '请输入电话号码',
						duration: 2000
					});
				} else {
					this.$http.appointment({
						name: this.username,
						phone: this.userphone,
						checkingTime: this.userlookhousetime
					}).then(res => {
						console.log(res);
					});
				}

			},
			onGotUserInfo(res){
				console.log(res);
				
			},
			previewImage(){
				  uni.previewImage({
				            urls:['http://rongchuang.sqhd850.cn/pic/static/code@2x.png'],
				            longPressActions: {
				                itemList: ['发送给朋友', '保存图片', '收藏'],
				                success: function(data) {
				                    console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
				                },
				                fail: function(err) {
				                    console.log(err.errMsg);
				                }
				            }
				        });
			}
		},

		mounted() {
			
			this.$http.banner().then(res => {
				this.bannerList = res.data;
			});
			this.$http.videoIndex().then(res => {
				this.video = res.data.videoUrl;
			});
			this.$http.getPhone().then(res => {
				this.phone = res.data;
			});
			this.$http.getMostNews().then(res=>{
								
								 	 this.memorabilia=res.data.memorabilia;
								 	 
								    this.actvity=res.data.actvity;
								 
			})
			// uni.login({
			//   provider: 'weixin',
			//   success:  (loginRes)=> {
			// 	this.$http.wxlogin(loginRes.code).then(res=>{
			// 		console.log(res);
			// 	})
			//   }
			// });
		}
	};
